import React, { Component } from 'react'

import mainLogoImg from '../../../Images/main-logo.svg'
import menuBarImg from '../../../Images/menu-bars.svg'
import closeImg from '../../../Images/close.svg'
import footerInstaImg from '../../../Images/footer-insta.svg'
import { Nav, Navbar } from 'react-bootstrap'


export default class navbar extends Component {
    constructor(props){
        super(props);
        this.state = {expanded: false}
    }
    handleExpanded(expanded) {
        console.log(expanded);
        this.setState({expanded: expanded})
    }
    render() {
        return (
            // <nav class="navbar navbar-expand-lg navbar-dark pt-3">
            <Navbar expand="lg" className="pt-3" variant="dark" onToggle={(expanded) => this.handleExpanded(expanded)}>
            <Navbar.Brand href="#home" className="main-logo"><img src={mainLogoImg} alt="Logo" /></Navbar.Brand>
            <Navbar.Toggle aria-controls="navbarSupportedContent" >
                {(this.state.expanded ? <img src={closeImg} alt="Menu Icon" id="close" /> : <img src={menuBarImg} alt="Menu Icon" id="bars"/> )}             
            </Navbar.Toggle>
            <Navbar.Collapse>
                <Nav as="ul" className="ml-auto">
                    <Nav.Item className="nav-list margin-sm-top">
                        <Nav.Link className="nav-list">Are you a barber?</Nav.Link>
                    </Nav.Item>
                    <Nav.Item className="nav-list">                    
                        <Nav.Link className="nav-list" href="http://www.getbarbr.com" target="_blank">Are you a client?</Nav.Link>
                    </Nav.Item>
                    <Nav.Item className="nav-list">                    
                        <div class="line mx-auto" />
                    </Nav.Item>
                    <Nav.Item className="nav-list">                    
                        <Nav.Link className="nav-list" href="mybookings.html">My bookings</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>                    
                        <div class="line mx-auto" />
                    </Nav.Item>
                    <Nav.Item className="nav-list display-sm">                    
                        <Nav.Link href="#">FAQs</Nav.Link>
                    </Nav.Item>
                    <Nav.Item className="nav-list display-sm">                    
                        <Nav.Link href="#">Chat with us</Nav.Link>
                    </Nav.Item>
                    <Nav.Item className="nav-list display-sm">                    
                        <Nav.Link href="mailto:support@barbr.me">support@barbr.me</Nav.Link>
                    </Nav.Item>
                    <Nav.Item className="nav-list display-sm">                    
                        <Nav.Link href="https://www.instagram.com/getbarbr"><img src={footerInstaImg} alt="" /></Nav.Link>
                    </Nav.Item>
                    <Nav.Item className="nav-list display-sm">                    
                        <Nav.Link href="#" className="nav-foot-text">C 2020 Barbr Ltd. All rights reserved.</Nav.Link>
                    </Nav.Item>
    
                </Nav>
            </Navbar.Collapse>
            {this.state.expanded && <div class="log-in pt-1"><a id="login-label" onclick="LoginButton()"> Log in</a></div> }            
        </Navbar>
        )
    }
}



