export const SERVICE_TOGGLE = 'cart/SERVICE_TOGGLE';
export const CLEAR = 'cart/CLEAR';

const initialState = {
    selectedServices: []
}

export default (state = initialState, action) => {
    switch (action.type) {
        case SERVICE_TOGGLE:
            if (state.selectedServices.indexOf(action.payload) > -1) {
                return {
                    ...state,
                    selectedServices: state.selectedServices.filter(x => x != action.payload)
                }
            }
            return {
                ...state,
                selectedServices: [...state.selectedServices, action.payload]

            }
            case CLEAR:
                return {
                    ...state,
                    selectedServices: []
                }
                default:
                    return state;
    }
}

export const toggleService = (serviceId) => {
    return {
            type: SERVICE_TOGGLE,
            payload: serviceId
        }
}

export const clearServices = () => {
    return {
            type: CLEAR,
        }
}