import React, { Component } from 'react'

export default class ServiceSelect extends Component {
    constructor(props){
        super(props);

        this.state = {
            filter: 'mobile'
        }
    }
    render() {
        return (
            <div style={{marginTop: '10px'}}>
                <div class="container-fluid" id="service-display" >
                    <div class="row align-items-center">
                        <div class="col-5 body-headings font-weight-bold mb-3"> Services </div>
                        <div class="col-7 link text-right pr-2 mb-3" id="services-toggle">
                            <a className={`btn star-clr btn-font ${this.state.filter == 'mobile' ? 'button-active' : ''}`} id="delivery" onClick={()=> {
                                if(this.state.filter !== 'mobile') {
                                    this.props.clearServices();
                                    this.setState({filter: 'mobile'})
                                }

                            }}>Delivery</a>
                            <a className={`btn star-clr btn-font ${this.state.filter == 'shop' ? 'button-active' : ''}`} id="shop" onClick={()=> {
                                if(this.state.filter !== 'shop') {
                                    this.props.clearServices();
                                    this.setState({filter: 'shop'})
                                }

                            }}>Shop</a>
                        </div>
                    </div>
                    <div class="row align-items-center" id="services">
                    {this.props.services[this.state.filter].map((service, i) => 
                        
                        <div className={`row container-fluid px-1 my-1 service-box service-height mx-auto ${this.props.selectedServices.indexOf(service.id) > -1 ? 'active2' : ''}`} key={i}>
                            <div class="col-12" onClick={() => this.props.toggleService(service.id)}>
						        <div class="row align-items-center">
								<div class="col-9 pl-2 pr-0 pl-4" style={{paddingLeft: "12px !important"}}>
									<div class="service-heading-font pt-2 pb-1">{service.name}</div>
									<div class="service-text py-1">{service.description ? service.description : ""}</div>
									<div class="service-text pt-1 pb-2">{service.price}.00 <span class="rating-clr"> ~ {service.duration}m </span></div>
								</div>
                                {
                                    service.image && 
                                    <div class="col-3 px-1 text-right">
                                        <img src={service.image} alt="" class="img-fluid img-size" />
                                    </div>
                                }
							
							</div>
					    </div>
                    </div>
                    )}

                    </div>
                </div>
            </div>
        )
    }
}


