import React from 'react'

import profileIconImg from '../../../Images/profile-icon.svg'

const getTotalReviews = (num) => {
    if (num > 1000)
        return "(1000+)";
    else if (num > 500)
		return "(500+)";
	else if (num > 100)
		return "(100+)";
	else if (num > 50)
		return "(50+)";
	else if (num > 10)
		return "(10+)";
	else
		return `(${num})`;
} 

export default ({profile}) => {
    return (
<div class="container-fluid my-3 ">
				<div class="row align-items-center">
					<div class="col-3 px-0 justify-content-center flex">
						<img src={profile.image} alt="" class="img-fluid profile-img" id="profile-image" />
					</div>
					<div class="col-9 pl-0 pt-2">
                        <div class="profile-name" id="profile-name">{profile.display_name}</div>
						<img src={profileIconImg} alt="" />
                        <span class="star-clr" id="average-profile">{profile.avg_rating.toFixed(1)}</span>
                        <span class="rating-clr" id="total-reviews+">{getTotalReviews(profile.number_reviews)}</span>
					</div>
				</div>
			</div>
    
)}
