
import React, { Component } from 'react'
import { Swiper, SwiperSlide } from 'swiper/react'
import SwiperCore, { Pagination, Navigation } from 'swiper'
import { Modal } from 'react-bootstrap'
import 'swiper/swiper.scss';
import moment from 'moment'

import reviewIcon from '../../../Images/review-icon.svg'
import ratingIcon from '../../../Images/Rating-icon.png'
import reviewCommentIcon from '../../../Images/review-comment-icon.svg'
import commentIcon from '../../../Images/comment-icon.png'
import closeIcon from '../../../Images/close1.svg';

SwiperCore.use([Pagination, Navigation])

const getStars = (rating) => {
    let i = 0;
    const results = [];
    while(i < rating) {
        results.push(<img src={reviewIcon} alt="" class="review-slide-img" key={i}></img>)   
        i++;
    }
    while(i < 5){
        results.push(<img src={ratingIcon} alt="" class="review-slide-img" key={i}></img>)
        i++;
    }

    return results;
}

const getReviewStars = (rating) => {
    let i = 0;
    const results = [];
    while(i < rating) {
        results.push(<img src={reviewCommentIcon} alt="" key={i}></img>)   
        i++;
    }
    while(i < 5){
        results.push(<img src={commentIcon} alt="" key={i}></img>)
        i++;
    }

    return results;
}



export default class Reviews extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showModal: false
        }
    }

    setShow(show) {
        this.setState({showModal: show})
    }
    render() {
        const slides = this.props.reviews.map((review, i) => 
            <SwiperSlide className="review-slide pt-1 pb-0 px-4 mx-2 mb-2 mt-0" key={i}>
                {getReviewStars(review.rating)}
                <span class="rating-clr review-name-font-size pl-1">{review.customer_name ? review.customer_name : ""}.</span>
                <div class="review-font-size">{review.review_note ? review.review_note : "-"}</div>
                <div class="review-day-font-size rating-clr pt-1"> {moment(review.reviewed_at).fromNow()} </div>
            </SwiperSlide>
        );
        return (
            <div>
                <div class="container-fluid">
                    <div class="mt-4 mb-3 body-headings p-left"> Reviews </div>
                    <div class="row align-items-end">
                        <div class="col-5">
                            <span class="review-rating-font" id="average-main">{this.props.profile.avg_rating.toFixed(1)}</span>
                            <span class="avg"> AVG </span>
                        </div>
                        <div class="col-7 text-right mb-2 pr-2" onClick={() => this.setShow(true)}>
                            <div class="col-12" id="avg-review-stars">
                                {getStars(this.props.profile.avg_rating)}
                            </div>
                            <div class="col-12 font-size1 pt-2"> Based on <span class="star-clr" id="total-reviews-main"> {this.props.profile.number_reviews} reviews
                                </span> </div>
                        </div>
                    </div>
                </div>


                <Swiper centeredSlides={true} effect="coverflow" grabCursor={true} slidesPerView="auto" loop={false} navigation pagination>
                    {slides}
                </Swiper>
            
                <Modal className="reviews-modal-content" show={this.state.showModal} onHide={() => this.setShow(false)}>
                    <Modal.Body>
                    <div class="row align-items-center mt-3">
							<div class="col-2 text-left review-close">
								<button type="button" class="close close-btn mb-2" data-dismiss="modal" aria-label="Close" onClick={() => this.setShow(false)}>
									<img src={closeIcon} />
								</button>
							</div>
							<div class="col-9 text-center pl-0 review-head">Reviews <span id="rev-total">({this.props.profile.number_reviews})</span></div>
						</div>
						<div class="modal-body pt-2" id="list-reviews">
                            {slides}
						</div>
                    </Modal.Body>

                    </Modal>

            </div>
        )
    }
}

