import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { requestProfileAsync } from '../../modules/profile'
import { toggleService, clearServices } from '../../modules/cart'

			
import React, { Component } from 'react'
import Navbar from './components/navbar';
import ProfileHead from './components/profile-head';
import ProfileInfo from './components/profile-info';
import Address from './components/address';
import Styles from './components/styles';
import Reviews from './components/reviews';
import ServicesSelect from './components/services-select';
import CartBtn from './components/cart-btn'
import Footer from './components/footer'

class Profile extends Component {
	componentDidMount() {
		if(!this.props.profile.profile){
			this.props.requestProfileAsync(this.props.match.params.username);
		}
	}
	render() {
			const profile = this.props.profile.profile;
			if(!profile)	{
				return <div></div>;
			}
						
			const reviews = this.props.profile.reviews;
			const services = this.props.profile.services;
			return (	
		<div>
			<Navbar/>
			<ProfileHead profile={profile}></ProfileHead>
			<ProfileInfo profile={profile}></ProfileInfo>
			<div class="border line-margin" ></div>
			<Address profile={profile}></Address>

			<div class="container-fluid" id="desc-info">
			<div class="rating-clr px-0 description-padding font" id="top">{profile.about}</div>
			</div>

			<div class="border line-margin" id="desc-info-border"></div>
			<Styles profile={profile}></Styles>
			
			{
				reviews && reviews.length > 0 &&			
				<Reviews profile={profile} reviews={reviews}></Reviews>
			}

			{
				services &&			
				<ServicesSelect services={services} selectedServices={this.props.cart.selectedServices} toggleService={this.props.toggleService} clearServices={this.props.clearServices}></ServicesSelect>
			}

			<div class="modal fade" id="exampleModal1" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
				aria-hidden="true">
				<div class="modal-dialog" role="document">
					<div class="modal-content modal-top mx-auto">
						<div class="modal-header text-center">
							<div class="row">
								<div class="col-12 text-center modal-head my-4" id="barber-name"></div>
								<div class="col-12 mt-2 mb-0">
									<section class='rating-widget'>

										<div class='rating-stars text-center'>
											<ul id='stars'>
												<li class='star' id="star-0" title='Poor' data-value='1'>
													<img src={".Images/favouriteGrey.svg"} alt="" />
												</li>
												<li class='star' id="star-1" title='Fair' data-value='2'>
													<img src={".Images/favouriteGrey.svg"} alt="" />
												</li>
												<li class='star' id="star-2" title='Good' data-value='3'>
													<img src={".Images/favouriteGrey.svg"} alt="" />
												</li>
												<li class='star' id="star-3" title='Excellent' data-value='4'>
													<img src={".Images/favouriteGrey.svg"} alt="" />
												</li>
												<li class='star' id="star-4" title='WOW!!!' data-value='5'>
													<img src={".Images/favouriteGrey.svg"} alt="" />
												</li>
											</ul>
										</div>
									</section>
								</div>
							</div>
						</div>
						<div class="modal-body modal-height">
							<textarea placeholder="Leave a comment" class="textarea p-1" id="comment-textarea"></textarea>
						</div>
						<div class="modal-footer">
							<button type="button" class="btn review-btn" data-dismiss="modal"
								onclick="confirmSubmit()">SUBMIT</button>
						</div>
					</div>
				</div>
			</div>

			<div class="mt-4 footer">
				<Footer></Footer>

				{this.props.cart.selectedServices.length && <CartBtn selectedServices={this.props.cart.selectedServices} profile={profile}></CartBtn>}

			</div>
		</div>
		)
	}
}

const mapStateToProps = ({ profile, reviews, cart }) => ({
  profile,
  reviews,
  cart
})

const mapDispatchToProps = dispatch => bindActionCreators({
  requestProfileAsync,
  toggleService,
  clearServices
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Profile)