import React, { Component } from 'react'

export default class CartBtn extends Component {
    render() {
        const totalPrice = this.props.profile.services.filter(service => this.props.selectedServices.indexOf(service.id) > -1).reduce((total, service) => {return total + service.price}, 0);
        return (
            <div className="cart-btn" id="one">
					<a id="foot-btn" onclick="bookservice()">
						<div class="flex1">
							<div class="numbers">{this.props.selectedServices.length}</div>
							<div class="book-weight">Book</div>
							<div class="price">{totalPrice}.00</div>
						</div>
					</a>
				</div>
        )
    }
}
