import moment from 'moment'
import angleDownImg from '../../../Images/angle-down.svg'

import React, { Component } from 'react'
import Collapse from "@kunukn/react-collapse";

import '../../../css/custom.css';

const getShopAddress = (data) => {
    let shopAddress;
    data.addresses && data.addresses.length > 0 && data.addresses.forEach(address => {
        if (address.address_type == "shop")
        shopAddress = address;
    });

    return shopAddress
}

const OpenMap = url => {
    var a = window.document.createElement("a");
			a.target = '_blank';
			a.href = url;
			// Dispatch fake click
			var e = window.document.createEvent("MouseEvents");
			e.initMouseEvent("click", true, true, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
			a.dispatchEvent(e);
}

export default class Address extends Component {
    constructor(props){
        super(props)
        this.state = {timeCollapsed: true}
    }
    render() {
        const profile = this.props.profile;
        const shopAddress = getShopAddress(profile);
        return (
            <div>
            <div class="container-fluid mt-3 pt-2">
				<div class="row flex align-items-center">
					<div class=" col-1 adress-icon"></div>
					<div class="col-9 pl-2 address-font" id="address-add">{shopAddress.formatted_name}</div>
					<a class="col-2 pl-0 star-clr map-padding text-center" id="google-map-address" onClick={() => OpenMap(`https://www.google.com/maps/?q=${shopAddress.formatted_name}`)}> Map </a>
				</div>
			</div>
			<div className={`mb-3 pb-2 ${this.state.timeCollapsed ? '' : 'bg-clr'}`} id="time">
				<div class="container-fluid">
					<div class="row flex align-items-center mt-3 pt-2">
						<div class="col-1 adress-icon"></div>
						<div class="col-9 pl-2 address-font " id="all-time"> {`${moment("2020-12-23T" + profile.min_from_time).format('hh:mm A')} - ${moment("2020-12-23T" + profile.max_to_time).format('hh:mm A')} `} </div>
						<div class="col-2 pl-0 angle-padding text-center">
                            <img src={angleDownImg} alt="" id="angle-down" className={this.state.timeCollapsed ? "" : "rotate"} onClick={()=>this.setState({timeCollapsed: !this.state.timeCollapsed})} />
                        </div>
					</div>

                    <Collapse isOpen={!this.state.timeCollapsed} transition={`height 280ms cubic-bezier(.4, 0, .2, 1)`}>
                        <div class="row" id="timing-transition" >
                            <div class="col-4">
                                <div class="row days-margin mb-3">
                                    <div class="col-12 days-transition mt-2">Monday</div>
                                    <div class="col-12 days-transition">Tuesday</div>
                                    <div class="col-12 days-transition">Wednesday</div>
                                    <div class="col-12 days-transition">Thursday</div>
                                    <div class="col-12 days-transition">Friday</div>
                                    <div class="col-12 days-transition">Saturday</div>
                                    <div class="col-12 days-transition">Sunday</div>
                                </div>
                            </div>
                            <div class="col-8">
                                <div class="row pl-2 ml-3 text-right pr-2 mt-2">
                                    <div class="col-12 days-transition" id="Monday">Closed</div>
                                    <div class="col-12 days-transition" id="Tuesday">Closed</div>
                                    <div class="col-12 days-transition" id="Wednesday">Closed</div>
                                    <div class="col-12 days-transition" id="Thursday">Closed</div>
                                    <div class="col-12 days-transition" id="Friday">Closed</div>
                                    <div class="col-12 days-transition" id="Saturday">Closed</div>
                                    <div class="col-12 days-transition" id="Sunday">Closed</div>
                                </div>
                            </div>

                        </div>
                    </Collapse>
				</div>
			</div>
            
            {this.state.timeCollapsed && <div class="border line-margin" ></div>}
            
			
        </div>
        )
    }
}
