import React from 'react'

export default ({profile}) => {
    return (
        <div class="rel">
        <img src={profile.portfolio.cover_image} alt="" class="img-fluid head-img" id="cover-image" />
        <div class="head-review">
        <div class="text-center text-white semibold" id="average"> {profile.avg_rating.toFixed(1)} </div>
                <div class="text-center text-white head-regular-font" id="total-reviews"> {profile.number_reviews} reviews </div>
            </div>
        </div>
    )
}
