import React from 'react'

import footerLogoImg from '../../../Images/footer-logo.svg';
import footerInstaImg from '../../../Images/footer-insta.svg';

export default () => {
    return (
        <div>
            <img src={footerLogoImg} alt="" class="footer-img pl-1" />

				<div class="row pading-top pl-3 mr-0 pr-0">
					<div class="col-6 footer-heading">About</div>
					<div class="col-6 footer-heading">Help</div>
					<div class="col-6"><a href="http://www.getbarbr.com" target="_blank"> For Barbers </a></div>
					<div class="col-6"><a href=""> Privacy Policy </a></div>
					<div class="col-6"><a href="http://www.getbarbr.com/clients" target="_blank"> For Customers </a></div>
					<div class="col-6"><a href=""> Terms of Service </a></div>
					<div class="col-6"><a href=""> FAQs </a></div>
					<div class="col-6"><a href=""> Get in Touch </a></div>
					<div class="col-6"><a href="https://www.instagram.com/getbarbr" target="_blank"> <img
								src={footerInstaImg} alt="" /> </a></div>
				</div>
        </div>
    )
}
