// import {  api} from "../api";

const api = require('../api').default;

export const PROFILE_REQUESTED = 'profile/PROFILE_REQUESTED'
export const PROFILE_LOADED = 'profile/PROFILE_LOADED'
export const REVIEWS_REQUESTED = 'profile/REVIEWS_REQUESTED'
export const REVIEWS_LOADED = 'profile/REVIEWS_LOADED'
export const SERVICES_LOADED = 'profile/SERVICES_LOADED'


const initialState = {
  profile: null,
  reviews: null,
  services: null
}

export default (state = initialState, action) => {
  switch (action.type) {
    case PROFILE_LOADED:
      return {
        ...state,
        profile: action.payload
      }
      case REVIEWS_LOADED: 
      return {
        ...state,
        reviews: action.payload
      }
      case SERVICES_LOADED: {
        return{
          ...state,
          services: action.payload
        }
      }

      default:
        return state
  }
}


export const requestProfileAsync = (name) => {
  return dispatch => {
    dispatch({
      type: PROFILE_REQUESTED
    });
    return api.get(`/barber/${name}/`)
      .then(response => {
        dispatch(loadProfile(response.data));
        return response.data;
      })
      .then(data => {
        dispatch(loadServices(data.services));
        return data;
      })
      .then(data => {
        dispatch(requestReviewsAsync(data.user));
      })

  }
}

export const loadProfile = (data) => {
  return {
    type: PROFILE_LOADED,
    payload: data
  }
}

export const loadServices = (services) => {
  return {
    type: SERVICES_LOADED,
    payload: {
      mobile: services.filter(s=>s.location_type == 'mobile'),
      shop: services.filter(s=>s.location_type == 'shop'),
      all: services
    }
  }
}

export const requestReviewsAsync = (user) => {
  return dispatch => {
    dispatch({
      type: REVIEWS_REQUESTED
    });
    return api.get(`/bookings/barber/${user}/reviews`)
      .then(response => {
        dispatch(loadReviews(response.data.results));
      })
  }
}

export const loadReviews = (data) => {
  return {
    type: REVIEWS_LOADED,
    payload: data
  }
}