import React, { Component } from 'react'
import {Swiper, SwiperSlide} from 'swiper/react'
import 'swiper/swiper.scss';
export default class Styles extends Component {
    render() {
        return (
            <div>
                <div class="container-fluid">
                    <div class="font-weight-bold mt-3 mb-4 body-headings"> Styles </div>
                </div>
                <div className="swiper">
                { this.props.profile.portfolio.images.length > 0 &&
                    <Swiper effect={'coverflow'} grabCursor={true} centeredSlides={true} slidesPerView={'auto'} loop={'true'}>
                         {this.props.profile.portfolio.images.map(img => 
                             <SwiperSlide key={img.id} style={{width: '241px', height: '241px'}}><img src={img.image} style={{width: '241px', borderRadius: '6px'}}/></SwiperSlide>
                         )}
                    </Swiper>
                }
                </div>
            </div>
        )
    }
}


